body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.crypto-priceList-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30%;
  overflow: auto;
  overflow-x: hidden; }
  .crypto-priceList-container hr {
    width: 100%;
    border: 0.5px solid #f9f9f91c; }
  .crypto-priceList-container .crypto-priceList-container-item {
    display: flex;
    justify-content: space-between; }
    .crypto-priceList-container .crypto-priceList-container-item .crypto-priceList-container-item-col {
      font-size: calc(4px + 2vmin);
      width: 60%;
      line-height: 0.8em; }
    .crypto-priceList-container .crypto-priceList-container-item .crypto-priceList-currency {
      font-size: calc(4px + 2vmin);
      width: 125%;
      line-height: 0.8em;
      color: #f9f9f9; }
    .crypto-priceList-container .crypto-priceList-container-item .crypto-priceList-container-item-close {
      vertical-align: middle;
      color: rgba(248, 241, 255, 0.72) !important;
      height: auto; }

@media screen and (max-width: 800px) {
  .crypto-priceList-container {
    margin-top: calc(100%/15);
    margin-bottom: 6%; }
    .crypto-priceList-container .crypto-priceList-container-item {
      margin-top: 10%; } }

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  overflow: none; }
  .App .App-layout {
    background-image: url(/JackCasinoBit/casino-app//static/media/bg.84e1352a.png);
    background-position: 450%;
    background-size: 84%;
    background-repeat: no-repeat;
    background-color: #360863;
    color: white;
    max-height: 85vh; }
    .App .App-layout .App-content {
      width: 90%;
      display: flex;
      max-height: 84vh;
      min-height: 84vh;
      margin: auto; }
      .App .App-layout .App-content .App-content-info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start; }
        .App .App-layout .App-content .App-content-info .App-content-info-container {
          width: 125%; }
          .App .App-layout .App-content .App-content-info .App-content-info-container .App-content-info-paragraph {
            color: #f8f1ffb8;
            display: block; }
          .App .App-layout .App-content .App-content-info .App-content-info-container .App-content-info-mobile {
            display: none; }
        .App .App-layout .App-content .App-content-info .App-content-info-paragraph {
          color: #f8f1ffb8; }
        .App .App-layout .App-content .App-content-info .App-content-info-mobile {
          display: none; }
  .App .App-footer {
    margin: auto;
    width: 80%; }
  .App .App-content-form {
    margin-top: 5%; }
  .App .App-content-center {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center; }
    .App .App-content-center img {
      width: calc(100% - 20px);
      max-height: 80%; }

@media screen and (max-width: 850px) {
  .App {
    background-position: -20%;
    background-size: 360%; }
    .App .App-layout {
      max-height: none;
      margin-bottom: 5%; }
      .App .App-layout .App-content {
        margin-bottom: 5%;
        flex-direction: column;
        max-height: none; }
        .App .App-layout .App-content .App-content-info {
          margin-top: 5%; }
          .App .App-layout .App-content .App-content-info .App-content-info-container {
            max-width: 100%; }
            .App .App-layout .App-content .App-content-info .App-content-info-container .App-content-info-paragraph {
              display: none; }
            .App .App-layout .App-content .App-content-info .App-content-info-container .App-content-info-mobile {
              color: #f8f1ffb8;
              display: block; }
        .App .App-layout .App-content .App-content-center {
          display: none; }
    .App .App-content-form {
      margin-top: unset; } }

